.selector-toggle {
    position: relative
}

.pcoded .selector-toggle>a {
    position: absolute;
    top: 0;
    width: 60px;
    height: 60px;
    display: block;
    cursor: pointer;
    text-align: center;
    background: #FF5370;
    color: #fff;
    -webkit-box-shadow: 0 1px 4px 0 rgba(0,0,0,0.2);
    box-shadow: 0 1px 4px 0 rgba(0,0,0,0.2);
    padding: 14px 10px
}

.pcoded[theme-layout="horizontal"] .selector-toggle>a,.pcoded[vertical-placement="left"] .selector-toggle>a {
    left: -80px;
    border-right: 0;
    border-radius: 50% 0 0 50%
}

.pcoded[vertical-placement="right"] .selector-toggle>a {
    right: -80px;
    border-left: 0;
    border-radius: 0 50% 50% 0
}

.pcoded[vertical-placement="right"] .selector-toggle>a:before {
    left: 10px
}

.selector-toggle>a:before {
    font-family: 'feather';
    font-size: 20px;
    content: "\e8c3";
    position: relative;
    float: left;
    left: 13px;
    line-height: 35px;
    transition: 0.5s;
    -webkit-transition: 0.5s;
    -ms-transition: 0.5s;
    -moz-transition: 0.5s;
    -o-transition: 0.5s;
    -webkit-animation: mymove 1.3s infinite linear;
    animation: mymove 1.3s infinite linear
}

.open .selector-toggle>a:before {
    content: "\e8f6";
    -webkit-animation: mymove 0s infinite linear;
    animation: mymove 0s infinite linear
}

#styleSelector .nav-tabs {
    margin-bottom: 20px;
    border-bottom: 2px solid #4099ff;
    padding-bottom: 5px
}

#styleSelector .nav-tabs .nav-item {
    width: calc(100% / 2);
    margin: 0;
    padding: 0;
    float: left
}

#styleSelector .nav-tabs .nav-item a {
    color: #37474f;
    text-align: center;
    font-weight: 500;
    font-size: 13px;
    padding: 0.66rem 1rem;
    border-radius: 2px;
    border: none;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out
}

#styleSelector .nav-tabs .nav-item a.active {
    color: #fff;
    background: #4099ff
}

#styleSelector .style-cont {
    background-color: #F6F7FB;
    padding: 10px
}

#styleSelector .st-main-title {
    font-size: 18px
}

#styleSelector .checkbox-fade label {
    margin-bottom: 0.8rem
}

#styleSelector .sub-title {
    text-transform: capitalize
}

#styleSelector #menu-effect {
    padding-bottom: 15px
}

#styleSelector #menu-effect .radio>label {
    padding-left: 0
}

