/*# sourceMappingURL=chartist.css.map */
body {
    background-color: #f2f7fb;
    font-size: .9375rem;
    overflow-x: hidden;
    color: #333;
    font-family: "Open Sans",sans-serif;
    background-image: linear-gradient(rgba(255,255,255,0.7) 240px, #f2f7fb 0%)
}

ul {
    padding-left: 0;
    list-style-type: none;
    margin-bottom: 0
}

*:focus {
    outline: none
}

a {
    font-size: 13px;
    color: #37474f;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out
}

@media only screen and (min-width: 1400px) {
    a {
        font-size:14px
    }
}

a:focus,a:hover {
    text-decoration: none;
    color: #4099ff
}

h1,h2,h3,h4,h5,h6 {
    font-weight: 500;
    font-family: "Quicksand",sans-serif
}

h1 {
    font-size: 2.5rem
}

h2 {
    font-size: 36px
}

h3 {
    font-size: 24px
}

h4 {
    font-size: 20px
}

h5 {
    font-size: 18px
}

h6 {
    font-size: 16px
}

p {
    font-size: 14px
}

b,strong {
    font-weight: 500
}

code {
    padding: 0 3px;
    border-radius: 3px
}

.img-radius {
    border-radius: 50%;
    border: 2px solid #fff;
    box-shadow: 0 5px 10px 0 rgba(43,43,43,0.2)
}

.media-left {
    padding-right: 20px
}

@media only screen and (max-width: 575px) {
    h1 {
        font-size:2.0rem
    }

    h2 {
        font-size: 30px
    }

    h3 {
        font-size: 22px
    }

    h4 {
        font-size: 18px
    }

    h5 {
        font-size: 16px
    }

    h6 {
        font-size: 14px
    }

    p {
        font-size: 13px
    }
}

.main-body .page-wrapper {
    padding: 0.6rem 1.5rem;
    -webkit-transition: all ease-in 0.3s;
    transition: all ease-in 0.3s
}

.card {
    border-radius: 5px;
    box-shadow: 0 0 5px 0 rgba(43,43,43,0.1),0 11px 6px -7px rgba(43,43,43,0.1);
    border: none;
    margin-bottom: 30px;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out
}

.card .card-footer {
    border-top: none;
    padding: 15px 20px;
    border-radius: 0 0 10px 10px
}

.card .card-header {
    background-color: transparent;
    border-bottom: none;
    padding: 20px;
    position: relative
}

.card .card-header.borderless {
    border-bottom: 0 solid transparent
}

.card .card-header.borderless+.card-block,.card .card-header.borderless+.card-block-big,.card .card-header.borderless+.card-block-small {
    padding-top: 0
}

.card .card-header .card-header-left {
    display: inline-block
}

.card .card-header .card-header-right {
    right: 10px;
    top: 13px;
    display: inline-block;
    float: right;
    padding: 7px 0;
    position: absolute
}

@media only screen and (max-width: 575px) {
    .card .card-header .card-header-right {
        display:none
    }
}

.card .card-header .card-header-right i {
    margin: 0 4px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 500;
    color: #333;
    line-height: 1
}

.card .card-header .card-header-right .card-option {
    width: 35px;
    height: 20px;
    overflow: hidden;
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out
}

.card .card-header .card-header-right .card-option li {
    display: inline-block
}

.card .card-header span {
    display: block;
    font-size: 13px;
    margin-top: 5px
}

.card .card-header h5 {
    margin-bottom: 0;
    color: #333;
    font-size: 15px;
    font-weight: 700;
    display: inline-block;
    margin-right: 10px;
    line-height: 1.1;
    position: relative
}

.card .card-header h5+span {
    display: none
}

.card .card-header h5:after {
    content: "";
    background-color: #d2d2d2;
    width: 60px;
    height: 1px;
    position: absolute;
    bottom: -20px;
    left: 0
}

.card .card-block {
    padding: 1.25rem
}

.card .card-block.card-p-0 {
    padding: 1.25rem 0 0
}

.card .card-block.p-b-0 {
    padding-bottom: 0
}

.card .card-block table tr {
    padding-bottom: 20px
}

.card .card-block .sub-title {
    font-size: 14px;
    font-weight: 500;
    letter-spacing: 1px
}

.card .card-block code {
    background-color: #eee;
    margin: 5px;
    display: inline-block
}

.card .card-block .dropdown-menu {
    top: 38px
}

.card .card-block p {
    line-height: 1.4
}

.card .card-block a.dropdown-item {
    margin-bottom: 0;
    font-size: 14px;
    -webkit-transition: 0.25s;
    transition: 0.25s
}

.card .card-block a.dropdown-item:active,.card .card-block a.dropdown-item .active {
    background-color: #4099ff
}

.card .card-block.remove-label i {
    margin: 0;
    padding: 0
}

.card .card-block.button-list span.badge {
    margin-left: 5px
}

.card .card-block .dropdown-menu {
    background-color: #fff;
    padding: 0
}

.card .card-block .dropdown-menu .dropdown-divider {
    background-color: #ddd;
    margin: 3px 0
}

.card .card-block .dropdown-menu>a {
    padding: 10px 16px;
    line-height: 1.429
}

.card .card-block .dropdown-menu>li>a:focus,.card .card-block .dropdown-menu>li>a:hover {
    background-color: rgba(202,206,209,0.5)
}

.card .card-block .dropdown-menu>li:first-child>a:first-child {
    border-top-right-radius: 4px;
    border-top-left-radius: 4px
}

.card .card-block .badge-box {
    padding: 10px;
    margin: 12px 0
}

.card .card-block-big {
    padding: 1.5625rem
}

.card .card-block-small {
    padding: 0.9375rem
}

.card-footer {
    background: transparent
}

.pcoded .card.full-card {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99999;
    box-shadow: 0 0 35px 0 rgba(0,0,0,0.44);
    border-radius: 0;
    width: calc(100vw - 17px);
    height: 100vh
}

.pcoded .card.full-card.card-load {
    position: fixed
}

.pcoded .card.card-load {
    position: relative;
    overflow: hidden
}

.pcoded .card.card-load .card-loader {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    background-color: rgba(255,255,255,0.7);
    z-index: 999
}

.pcoded .card.card-load .card-loader i {
    margin: 0 auto;
    color: #4099ff;
    font-size: 20px
}

.rotate-refresh {
    -webkit-animation: mymove 0.8s infinite linear;
    animation: mymove 0.8s infinite linear;
    display: inline-block
}

@-webkit-keyframes mymove {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg)
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg)
    }
}

@keyframes mymove {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg)
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg)
    }
}

.pcoded-content {
    padding-top: 1px
}

.page-header.card {
    background-size: cover;
    position: relative;
    margin: 30px 35px 15px;
    background-color: transparent;
    box-shadow: none;
    padding: 0
}

.page-header.card h5 {
    color: "Quicksand",sans-serif;
    margin-bottom: 0
}

.page-header.card h5+span {
    font-size: 13px
}

.page-header.card .page-header-title i {
    display: inline-flex;
    float: left;
    width: 40px;
    height: 40px;
    border-radius: 5px;
    justify-content: center;
    vertical-align: middle;
    font-size: 22px;
    color: #fff;
    box-shadow: 0 2px 12px -3px rgba(0,0,0,0.5);
    margin-right: 20px;
    align-items: center
}

.page-header.card .breadcrumb {
    background: transparent;
    margin-bottom: 0;
    float: right
}

.page-header.card .breadcrumb a {
    font-size: 13px
}

.page-header.card .page-block {
    padding: 1.75rem 1.25rem 0.8rem
}

.page-header.card .page-block .breadcrumb {
    background: transparent;
    margin-bottom: 0;
    padding: 0
}

.page-header.card .page-block .breadcrumb a {
    font-size: 14px;
    color: "Quicksand",sans-serif
}

.page-header.card .page-block .breadcrumb .breadcrumb-item+.breadcrumb-item:before {
    font-family: "feather";
    content: "\e844";
    color: "Quicksand",sans-serif
}

.page-header.card .page-block .breadcrumb .breadcrumb-item:last-child a {
    color: "Quicksand",sans-serif;
    font-weight: 600
}

@media only screen and (max-width: 768px) {
    .page-header.card .page-block .breadcrumb {
        float:left;
        margin-top: 10px;
        float: none
    }
}

@media only screen and (max-width: 991px) {
    .page-header.card .align-items-end .page-header-title {
        text-align:center;
        display: block
    }

    .page-header.card .align-items-end .page-header-title i {
        float: none;
        margin-bottom: 10px;
        margin-right: 0
    }

    .page-header.card .align-items-end .page-header-breadcrumb {
        width: 100%;
        text-align: center;
        margin-top: 10px;
        display: block
    }

    .page-header.card .page-header-breadcrumb .breadcrumb-item {
        display: inline-block;
        float: none
    }
}

.sub-title {
    border-bottom: 1px solid rgba(204,204,204,0.35);
    padding-bottom: 10px;
    margin-bottom: 20px;
    font-size: 14px;
    font-weight: 400;
    color: #2c3e50
}

.blockquote {
    border-left: 0.25rem solid #eceeef;
    padding: 0.5rem 1rem
}

.blockquote.blockquote-reverse {
    text-align: right;
    padding-right: 1rem;
    border-right: 0.25rem solid #eceeef;
